
import { mapActions, mapMutations } from 'vuex'
export default {
  created () {
    this.initPage()
  },
  methods: {
    ...mapMutations('weather', {
      setLocation: 'SET_LOCATION'
    }),
    ...mapActions('weather', ['getWeather']),
    async initPage() {
      const location = await this.getLocation()
      this.setLocation({
        location
      })
      this.getWeather()
    },
    async getLocation() {
      return await new Promise((resolve, reject) => {
        wx.getLocation({
          success(location) {
            resolve(location)
          },
          fail(err) {
            console.log(err)
            reject(err)
          }
        })
      })
    }
  }
}
